import { useEffect, useState } from 'react';
import './App.css';
// import contractVRF from './contracts/vrf.json';
import contractNFT from './contracts/nftMint.json';

import { ethers } from 'ethers';

// change the contract addresses after each deployment

const contractAddressNFT = '0xB06Cc6682767B4d357438d954E8d841f423fB802';

const abi = contractNFT.abi;

const truncateStr = (fullStr, strLen) => {
	if (fullStr.length <= strLen) return fullStr;
	const separator = '...';
	let separatorLength = separator.length;
	const charsToShow = strLen - separatorLength;
	const frontChars = Math.ceil(charsToShow / 2);
	const backChars = Math.floor(charsToShow / 2);
	return (fullStr.substring(0, frontChars) + separator + fullStr.substring(fullStr.length - backChars));
};

function App() {
	const [message, setMessage] = useState('');
	const [currentAccount, setCurrentAccount] = useState(null);
	// const [walletAddress, setWalletAddress] = useState('');

	const checkWalletIsConnected = async () => {
		const { ethereum } = window;
		if (!ethereum) {
			console.log(' Install the Metamask browser extension.');
			return;
		} else {
			setMessage('Wallet exists.');
		}

		const accounts = await ethereum.request({ method: 'eth_accounts' });

		if (accounts.length !== 0) {
			const account = accounts[0];
			setMessage('Using first account in wallet as default.');
			setCurrentAccount(account);
		} else {
			setMessage(' No authorized account found.');
		}
	};

	const connectWalletHandler = async () => {
		const { ethereum } = window;
		if (!ethereum) {
			setMessage(' Install the Metamask browser extension.');
		}

		try {
			const accounts = await ethereum.request({
				method: 'eth_requestAccounts',
			});

			setMessage('Using first account in wallet as default.');
			setCurrentAccount(accounts[0]);
			// setWalletAddress(accounts[0]);
		} catch (err) {
			console.log(err);
		}
	};

	const mintNftHandler = async () => {
		try {
			const { ethereum } = window;
			if (ethereum) {
				const provider = new ethers.providers.Web3Provider(ethereum);
				const signer = provider.getSigner();

				const nftContract = new ethers.Contract(contractAddressNFT, abi, signer);


				//TODO: call VRF contract to return random number (or the URI string)


				setMessage('Authorize payment to mint NFT.');

				//TODO: call safeMint(address to, uint256 tokenId, string memory uri)
				//TODO:               your MM account        0     ^^^^^^ get this from the VRF contract

				let nftTxn = await nftContract.mintNFTs(1, {
					value: ethers.utils.parseEther('0.00001'),
				});

				setMessage('Validating...');
				await nftTxn.wait();

				setMessage(` Etherscan tx: ${nftTxn.hash}`
				);
			} else {
				setMessage(' Ethereum object does not exist');
			}
		} catch (err) {
			console.log(err);
		}
	};

	const connectWalletButton = () => {
		return (
			<div>
				<button
					onClick={connectWalletHandler}
					className='cta-button connect-wallet-button'>
					Connect Wallet
				</button>
			</div>
		);
	};

	const mintNftButton = () => {
		return (
			<div>
				<h3 className='text-style'> Account Address: {truncateStr(currentAccount, 17)}</h3>
				<button onClick={mintNftHandler} className='cta-button mint-nft-button'>
					Mint NFT
				</button>
			</div>
		);
	};

	useEffect(() => {
		checkWalletIsConnected();
	}, []);
	return (
		<div className='card'>
			<div className='header'>
				<h3 className='text-color'>Sepolia NFT Minter</h3>
			</div>
			<hr></hr>
			<div className='container'>
				{currentAccount ? mintNftButton() : connectWalletButton()}
			</div>
			<div>
				<hr></hr>
				<h4>{message}</h4>
			</div>
		</div>
	);
}

export default App;
